@import "./scss_includes/variables";

html, body, .app {
  padding: 0;
  margin: 0;
  font-size: 12px;
  width: 100%;
  height: 100%;
  font-family: Arial;
  overflow: hidden;
}
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

.toolbar#mode {
  top: 20px;
  left: 20px;
}
.toolbar#stroke {
  top: 20px;
  right: 20px;
}
.toolbar#color {
  right: 20px;
  top: 240px;
}
.toolbar#trash {
  right: 20px;
  bottom: 20px;
}

.datatable {
  position: absolute;
  width: 80%;
  height: 80%;
  top: 10%;
  left: 10%;
  overflow-y: scroll;
  padding: 10px;
  background: $light-gray;
  display: none;

  &.open {
    display: block;
  }
}

.scrubbar {
  width: 100%;
  position: absolute;
  bottom: 50px;
  margin: 0 auto;
  display: none;
}